import{destroyer,styler}from"./base";import{exioPointerEffect}from"./effects";const exioCheckbox=e=>{const t=exioPointerEffect(e,{borderStyle:"static"}),o=styler(e),r=(o.innerHTML=` .${o.id} { -webkit-appearance: none; width: 20px; height: 20px; display: flex; align-items: center; justify-content: center; padding: 0px; background-color: var(--exio-selected-background-color); } .${o.id}:not(:checked) { background-color: transparent; } `,styler(e));return r.innerHTML=` .${r.id}::after { content: 'check'; font-family: 'Exio Icons'; position: absolute; width: ${e.clientHeight}px; height: ${e.clientHeight}px; transform: scale(0); display: flex; align-items: center; justify-content: center; color: var(--exio-selected-indicator-color); } .${r.id}:checked::after { transform: scale(1.1); } `,destroyer(()=>{t.destroy(),o.remove(),r.remove()})};export{exioCheckbox};